body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.btn {
  background:transparent;
  border:none;
  font-size:12pt;
}

.trans {
  animation: fadein 3s;
  -webkit-animation: fadein 3s;
}

@-webkit-keyframes fadein {
  from {opacity:0;}
  to {opacity:1;}
}

@keyframes fadein {
  from {opacity:0;}
  to {opacity:1;}
}
